import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class MedibuddyService {

  constructor(private http: HttpClient) { }

  // Specialization Info API's
  public getDoctorInfo()
  {
   return this.http.get(`${environment.baseURL}/doctor/getDoctorInfo`);
  }

  public saveDoctorInfo(reqData: any)
  {
   return this.http.post(`${environment.baseURL}/doctor/saveDoctorInfo`, reqData);
  }

  public updateDoctorInfo(reqData: any)
  {
   return this.http.post(`${environment.baseURL}/doctor/updateDoctorInfo`, reqData);
  }

  public deleteDoctorInfo(id: any)
  {
   return this.http.get(`${environment.baseURL}/doctor/deleteDoctorInfo?id=${id}`);
  }

  //Doctor Info API's
  public getSpecializationInfo()
  {
   return this.http.get(`${environment.baseURL}/specialization/getSpecializationInfo`);
  }

  public saveSpecializationInfo(reqData: any)
  {
   return this.http.post(`${environment.baseURL}/specialization/saveSpecializationInfo`, reqData);
  }

  public updateSpecializationInfo(reqData: any)
  {
   return this.http.post(`${environment.baseURL}/specialization/updateSpecializationInfo`, reqData);
  }

  public deleteSpecializationInfo(id: any)
  {
   return this.http.get(`${environment.baseURL}/specialization/deleteSpecializationInfo?id=${id}`);
  }

    //Patient Info API's
    public getPatientInfo()
    {
     return this.http.get(`${environment.baseURL}/patient/getPatientInfo`);
    }
  
    public savePatientInfo(reqData: any)
    {
     return this.http.post(`${environment.baseURL}/patient/savePatientInfo`, reqData);
    }
  
    public updatePatientInfo(reqData: any)
    {
     return this.http.post(`${environment.baseURL}/patient/updatePatientInfo`, reqData);
    }
  
    public deletePatientInfo(id: any)
    {
     return this.http.get(`${environment.baseURL}/patient/deletePatientInfo?id=${id}`);
    }



}
